/* ==========================================================================
   Author's custom styles
   ========================================================================== */
body {
  background: url("../_i/bg_body.jpg") top no-repeat;
  background-size: cover;
  height: 100%; }

ul {
  list-style: none;
  margin: 0;
  padding: 0; }

.container {
  display: flex;
  align-items: center;
  height: 100%;
  margin: 0 auto;
  max-width: 1400px; }

.calendar {
  box-shadow: 0 0 50px 0 rgba(0, 0, 0, 0.5);
  height: 90%;
  flex: 1 0 100%;
  position: relative;
  width: 100%; }
  .calendar:before, .calendar:after {
    content: "";
    display: table; }
  .calendar:after {
    clear: both; }

.calendar-block {
  background: #fff;
  height: 100%;
  float: left;
  width: 65%; }

.calendar-notes {
  background: #504aa4;
  background: linear-gradient(to bottom, #504aa4 0%, #4267b5 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#504aa4', endColorstr='#4267b5',GradientType=0 );
  height: 100%;
  float: left;
  width: 35%; }

header {
  background: #f5f5f3;
  display: flex;
  justify-content: space-between;
  height: 6rem;
  padding: 0 5.49%;
  position: relative; }

.calendar-main__title {
  color: #898d90;
  font: 300 2rem/6rem "Open Sans", sans-serif;
  margin: 0; }

.calendar-main__nav {
  align-self: center;
  white-space: nowrap; }
  .calendar-main__nav li {
    background: transparent;
    border: 1px solid transparent;
    border-radius: .5em;
    color: #898d90;
    cursor: pointer;
    display: inline-block;
    font: .75rem/1 "Open Sans", sans-serif;
    padding: .5em;
    transition: all .4s; }
    .calendar-main__nav li.active {
      background: #fff;
      border: 1px solid #e3e3e1;
      border-radius: .5em; }
  .calendar-main__nav li + li {
    margin-left: .5em; }

.calendar-main {
  margin: 6.5% auto 0;
  position: relative;
  width: 85%; }

.calendar__wrap .row:first-child {
  border-bottom: 2px solid #efefec;
  font-weight: bold;
  padding-bottom: .5rem; }

.row {
  display: flex;
  font: .8rem/2.5rem "Open Sans", sans-serif;
  justify-content: space-between;
  margin: 1rem auto;
  width: 85%; }

.col {
  color: #575e64;
  border-radius: 50%;
  background: #fff;
  height: 2.5rem;
  position: relative;
  text-align: center;
  text-transform: uppercase;
  transition: all .3s;
  width: 2.5rem; }
  .col.selected {
    box-shadow: 0 0 10px 0 #e75a47;
    color: #fff;
    background: #e75a47;
    font-weight: bold; }
  .col.contain {
    cursor: pointer; }
    .col.contain:hover {
      font-weight: bold; }
  .col.contain::before {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    background: #e8aa2e;
    border-radius: 50%;
    content: '';
    display: block;
    height: .3rem;
    margin: 0 auto;
    width: .3rem; }
  .col.contain.done::before {
    background: #2dbd9b; }
  .col.selected.contain::before {
    display: none; }

.calendar__prev {
  position: absolute;
  top: 50%;
  left: 0;
  background: url("../_i/icon_prev.png") center no-repeat;
  background-size: contain;
  cursor: pointer;
  height: 1.45rem;
  margin-top: -.75rem;
  width: .9rem; }

.calendar__next {
  position: absolute;
  top: 50%;
  right: 0;
  background: url("../_i/icon_next.png") center no-repeat;
  background-size: contain;
  cursor: pointer;
  height: 1.45rem;
  margin-top: -.75rem;
  width: .9rem; }

.calendar-count {
  border-top: 2px solid #eaeae6;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: nowrap;
  margin: 6.5% auto 0;
  padding-top: 6.5%;
  width: 85%; }

.count-list {
  flex: 1 0 auto; }

.count-list__item {
  display: inline-block;
  font: 1.2rem/1.4 "Open Sans", sans-serif;
  position: relative; }
  .count-list__item span {
    display: block;
    font-size: 2em;
    font-weight: 300; }
  .count-list__item.done {
    color: #2dbd9b; }
  .count-list__item.undone {
    color: #e75a47; }
  .count-list__item.all {
    color: #8c9094; }

.count-list__item + .count-list__item {
  margin-left: 10%; }
  .count-list__item + .count-list__item::before {
    position: absolute;
    top: 50%;
    left: -2rem;
    background: #d6d6ce;
    border-radius: 50%;
    content: '';
    height: .4rem;
    width: .4rem; }

.add_note {
  background: #2dbd9b;
  border-radius: 50%;
  cursor: pointer;
  height: 2.5rem;
  position: relative;
  transition: all .3s;
  width: 2.5rem; }
  .add_note::before {
    color: #fff;
    content: '+';
    display: block;
    font: bold 2rem/2.4rem "Open Sans", sans-serif;
    text-align: center; }
  .add_note:hover {
    box-shadow: 0 0 10px 0 #2dbd9b;
    transform: rotate(-90deg); }

.calendar-notes {
  color: #fff;
  font-family: "Open Sans", sans-serif; }
  .calendar-notes p {
    margin: 0;
    padding: 0; }

.calendar-notes__date {
  margin: 27% 0 0 25%; }

.date-title {
  font-size: 1rem;
  font-weight: 300; }

.date-subtitle {
  font-size: 2rem;
  line-height: .8; }

.notes-list {
  border-left: 2px solid rgba(255, 255, 255, 0.5);
  margin: 15% 0 0 12.5%;
  padding-left: 12.5%; }

.notes-list__item {
  font-weight: 300;
  position: relative; }
  .notes-list__item span {
    position: absolute;
    top: 0;
    left: -4.4rem;
    background: #e8aa2e;
    border-radius: .5em;
    display: inline-block;
    font-size: .75rem;
    padding: .3em .5em; }

.notes-list__item + .notes-list__item {
  margin-top: 7.5%; }

/* ==========================================================================
   Helper classes
   ========================================================================== */
.hidden {
  display: none !important; }

.invisible {
  visibility: hidden; }
